.container {
  max-width: 1366px;
  padding-left: 83px;
  padding-right: 83px;
  margin: 0 auto;
}
body {
  width: 100%;
  box-sizing: border-box;
}
.header-block {
  font-family: "Galien", "Arial", sans-serif;
}
.header-block__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-block__item {
  list-style: none;
}
.header-block__burger {
  display: flex;
  justify-content: center;
  user-select: none;
  margin: 0;
  border-radius: 0;
  font-size: 0;
  line-height: 0;
  outline: 0;
  cursor: pointer;
  box-sizing: content-box;
  width: 20px;
  padding: 10px;
  padding-right: 0;
  background: 0 0;
  border: 0;
}
.header-block__toggle {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 2px;
  vertical-align: bottom;
  transition: background 0.7s 0.7s;
  background-color: #356EAD;
}
.header-block__toggle::before,
.header-block__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #356EAD;
  transition: top 0.5s 0.5s, transform 0.5s, -webkit-transform 0.5s;
  transform-origin: 50% 50%;
}
.header-block__toggle::before {
  top: 8px;
}
.header-block__toggle::after {
  top: -8px;
}
.header-block__burger--open .header-block__toggle::before,
.header-block__burger--open .header-block__toggle::after {
  transition: top 0.3s, transform 0.3s 0.5s;
  width: 28px;
  top: 0;
  left: -5px;
}
.header-block__burger--open .header-block__toggle::before {
  transform: rotate3d(0, 0, 1, 45deg);
}
.header-block__burger--open .header-block__toggle::after {
  transform: rotate3d(0, 0, 1, -45deg);
}
.header-block__burger--open .header-block__toggle {
  background: 0 0;
  transition: background 0.1s 0.1s;
}
.description-block__heading {
  font-size: 64px;
  line-height: 74px;
  letter-spacing: 0.04em;
  color: #1F3F68;
}
.description-block__list {
  list-style: none;
}
.description-block {
  display: flex;
  flex-direction: row;
  background: linear-gradient(0deg, #98C3E8 8.84%, rgba(152, 195, 232, 0) 31.12%);
}
